@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-outfit);
  background-color: var(--bg1);
  color: white;
}
@media (min-width: 64em) {
  body {
    display: flex;
  }
}

@media (min-width: 64em) {
  main {
    flex: 1;
    margin-top: 64px;
  }
}

ul, li {
  list-style: none;
}

a {
  display: flex;
}

.flex {
  display: flex;
}

:root {
  --bg1: #10141E;
  --bg2: #161D2F;
  --red: #FC4747;
  --grey: #5A698F;
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--bg2);
  margin-bottom: 1.5rem;
  padding: 1.125rem 1rem;
}
@media (min-width: 40em) {
  .header {
    border-radius: 0.625rem;
    margin: 24px;
    padding: 1.25rem 1.5rem;
  }
}
@media (min-width: 64em) {
  .header {
    flex-direction: column;
    max-width: 96px;
    height: calc(100vh - 64px);
    border-radius: 1.25rem;
    margin: 32px;
    padding: 2.0625rem 1.75rem;
  }
}
@media (min-width: 40em) {
  .header__logo {
    width: 32px;
    height: 25px;
  }
}
@media (min-width: 64em) {
  .header__nav {
    margin: 4.6875rem 0;
    flex: 1;
  }
}
.header__nav-link path {
  transition: fill 0.15s ease-in-out;
}
.header__nav-link:hover path {
  fill: white;
}
.header__nav-link--active {
  fill: white;
}
.header__nav ul {
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .header__nav ul {
    gap: 2rem;
  }
}
@media (min-width: 64em) {
  .header__nav ul {
    flex-direction: column;
    gap: 2.5rem;
  }
}
.header__profile {
  border: 1px solid white;
  border-radius: 50%;
}
@media (min-width: 40em) {
  .header__profile {
    width: 32px;
    height: 32px;
  }
}
@media (min-width: 64em) {
  .header__profile {
    width: 40px;
    height: 40px;
  }
}

.search {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-end;
  gap: 16px;
}
.search-wrapper {
  width: calc(100% - 32px);
  margin-inline: auto;
}
@media (min-width: 40em) {
  .search-wrapper {
    width: calc(100% - 48px);
  }
}
@media (min-width: 64em) {
  .search-wrapper {
    width: calc(100% - 8px);
  }
}
.search-text {
  width: 100%;
  font-size: 1rem;
  font-weight: 300;
  color: white;
  padding: 14px 0;
}
.search-text::placeholder {
  color: white;
  opacity: 0.5;
  font-size: 1rem;
  font-weight: 300;
}
.search-text:focus {
  outline: none;
  border-bottom: 1px solid var(--grey);
}
.search-submit {
  display: flex;
  width: 24px;
}
.search-submit img {
  width: 100%;
}
@media (min-width: 40em) {
  .search-submit {
    width: 32px;
  }
}
.search button, .search input {
  background-color: transparent;
  border: none;
}